'use client';
import { Button } from '@/components/ui/button';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { zodResolver } from '@hookform/resolvers/zod';
import { useSearchParams } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { toast } from 'sonner';
import { useTransition, useState, useEffect } from 'react';
import * as z from 'zod';
import axios from 'axios';

const API_BASE_URL = "/api/v1";

const formSchema = z.object({
  username: z.string().min(1, "用户名不能为空"),
  password: z.string().min(1, "密码不能为空"),
  captcha: z.string().min(1, "验证码不能为空")
});

type UserFormValue = z.infer<typeof formSchema>;

export default function UserAuthForm() {
  const [loading, startTransition] = useTransition();
  const [captchaUrl, setCaptchaUrl] = useState("");
  const [captchaId, setCaptchaId] = useState("");

  const form = useForm<UserFormValue>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      username: '',
      password: '',
      captcha: ''
    }
  });

  // 获取验证码
  const refreshCaptcha = async () => {
    try {
      const response = await axios.get(`${API_BASE_URL}/captcha`);
      const data = response.data;
      console.log('验证码响应:', data);
      setCaptchaId(data.captcha_id);
      console.log('设置的captchaId:', data.captcha_id);
    } catch (err) {
      console.error('获取验证码失败:', err);
      toast.error("获取验证码失败");
    }
  };

  useEffect(() => {
    refreshCaptcha();
  }, []);

  const onSubmit = async (data: UserFormValue) => {
    startTransition(async () => {
      try {
        toast.info('正在登录...');
        const response = await fetch(`${API_BASE_URL}/login`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'captchaId': captchaId,
            'userInput': data.captcha
          },
          body: JSON.stringify({
            username: data.username,
            password: data.password
          })
        });

        const result = await response.json();
        
        if (result.token) {
          localStorage.setItem("token", result.token);
          toast.success('登录成功！即将跳转到主页...', {
            duration: 2000,
          });
          setTimeout(() => {
            window.location.href = '/dashboard';
          }, 2000);
        } else {
          toast.error(`登录失败: ${result.message || '未知错误'}`, {
            duration: 3000,
          });
          refreshCaptcha();
        }
      } catch (error) {
        toast.error('登录失败，请检查网络连接后重试', {
          duration: 3000,
        });
        refreshCaptcha();
      }
    });
  };

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="w-full space-y-2">
        <FormField
          control={form.control}
          name="username"
          render={({ field }) => (
            <FormItem>
              <FormLabel>用户名</FormLabel>
              <FormControl>
                <Input
                  type="text"
                  placeholder="请输入用户名"
                  disabled={loading}
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="password"
          render={({ field }) => (
            <FormItem>
              <FormLabel>密码</FormLabel>
              <FormControl>
                <Input
                  type="password"
                  placeholder="请输入密码"
                  disabled={loading}
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="captcha"
          render={({ field }) => (
            <FormItem>
              <FormLabel>验证码</FormLabel>
              <div className="flex space-x-2">
                <FormControl>
                  <Input
                    type="text"
                    placeholder="请输入验证码"
                    disabled={loading}
                    {...field}
                  />
                </FormControl>
                {captchaId && (
                  <img
                    src={`http://localhost:9333/api/v1/captcha/${captchaId}`}
                    alt="验证码"
                    className="h-10 cursor-pointer"
                    onClick={refreshCaptcha}
                  />
                )}
              </div>
              <FormMessage />
            </FormItem>
          )}
        />

        <Button disabled={loading} className="w-full" type="submit">
          登录
        </Button>
      </form>
    </Form>
  );
}